import React, { useEffect, useRef } from 'react';
import * as styles from './Intro.module.scss';
import { motion } from 'framer-motion';

const itemVariants = {
  hidden: { opacity: 0, translateY: -30 },
  visible: {
    opacity: 1,
    translateY: 0,
    transition: { ease: 'easeOut', duration: 0.6 },
  },
};

const letters = 'abcdefghijklmnopqrstuvwxyz';

const Item: React.FC<{ text: string }> = ({ text }) => {
  const el = useRef(null);
  useEffect(() => {
    const element = el.current as unknown as HTMLElement;

    if (!element) {
      return;
    }

    let iteration = 0;

    // clearInterval(interval);

    const interval = setInterval(() => {
      element.innerText = element.innerText
        .split('')
        .map((_, index) => {
          if (index < iteration) {
            return element.dataset.value[index];
          }

          return letters[Math.floor(Math.random() * 26)];
        })
        .join('');

      if (iteration >= element.dataset.value.length) {
        clearInterval(interval);
      }

      iteration += 1 / 3;
    }, 30);
  }, []);

  return (
    <motion.p data-value={text} variants={itemVariants} ref={el}>
      {text}
    </motion.p>
  );
};

export default function Intro() {
  return (
    <header className={styles.intro}>
      <motion.div
        className={styles.intro__eng}
        initial="hidden"
        animate="visible"
        variants={{
          visible: {
            transition: {
              staggerChildren: 0.1,
            },
          },
        }}
      >
        <Item text="Tomorrow is today" />
        <Item text="We come apart" />
        <Item text="We come together" />
        <Item text="Technology is human" />
        <Item text="Human is technology" />
        <Item text="Cities are connected" />
        <Item text="Cities are one" />
        <Item text="Human Tokyo" />
      </motion.div>

      <motion.div
        className={[styles.intro__jap, 'jap'].join(' ')}
        initial="hidden"
        animate="visible"
        variants={{
          visible: {
            transition: {
              staggerChildren: 0.1,
              delayChildren: 0.1,
            },
          },
        }}
      >
        <Item text="" />
        <Item text="" />
        <Item text="" />
        <Item text="" />
        <Item text="" />
        <Item text="" />
        <Item text="" />
        <Item text="" />
      </motion.div>
    </header>
  );
}
